
import { defineComponent } from 'vue'

export default defineComponent({
  name: "ModalLost",
  emits: ['new-game', 'cancel-action'],
  setup(props, context) {
    const clickYes = () => {
      context.emit('new-game')
    }
    const clickCancel = () => {
      context.emit('cancel-action')
    }

    return {
      clickYes,
      clickCancel,
    }
  }
})
